:root {

	/* Z-index levels */
}

.root {}

.slideIn,
.slideOut {
	-webkit-transition: -webkit-transform ease-in-out .2s;
	transition: -webkit-transform ease-in-out .2s;
	transition: transform ease-in-out .2s;
	transition: transform ease-in-out .2s, -webkit-transform ease-in-out .2s;
}

.underlayVisible {
	background: rgba(66, 66, 66, 0.30000000000000004);
	z-index: 400;
}

.content { }

.open { }

.pane {
	background-color: #fff;
	z-index: 401;
}

.title {
	position: relative;
}

.close {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	right: 35px;
	cursor: pointer;
	font-size: 0;
	border: none;
	background: none;
}

.close i {
	font-size: 14px;
}

.left { }

.right { }

.top { }

.bottom { }



